body {
  background-color: rgb(54, 52, 52);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

h2 {
  color: white;
}

.welcomeText {
  display: flex;
  justify-content: center;
  font-size: 3.5rem !important;
  font-weight: 300;
  color: white;
  padding: 10px 0px;
  text-align: center !important;
  width: fit-content;
  margin: auto;
}

h5 {
  font-size: 1.5rem;
}

.nav-container {
  display: flex;
  text-align: center;
  justify-content: space-evenly;
  margin: 15px auto;
}

.primary-header_links button {
  text-align: center;
  background-color: black;
  font-size: 1.5rem;
  margin: 25px;
  border-radius: 5px;
  padding: 0px 5px;
  box-shadow: 2px 2px 6px gray;
  transition: transform 0.5s ease;
  position: relative;
}

.primary-header_links button:hover {
  transform: scale(1.5);
  box-shadow: 4px 5px 6px gray;
}

.primary-header_links button:hover::after {
  content: "â˜…";
  position: absolute;
  top: -30px;
  left: 40%;
  font-size: 24px;
  color: gold;
  animation: stars 2s linear infinite;
}

.link {
  text-decoration: none;
  color: white;
}

.project-body-container {
  display: flex;
  flex-direction: column;
  color: white;
}

.image-container {
  margin-top: 15px;
  justify-content: center;
  text-align: center;
}

.image-container img {
  width: 250px;
  border-radius: 50%;
  box-shadow: 15px 5px 10px rgb(28, 41, 5);
}

.tech-skills-container {
  justify-content: center;
  text-align: center;
  margin: 25px;
}

.projects-container {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  max-width: 100vw;
  margin: 25px;
}

.projects-container li {
  list-style-type: none;
}

.button-link h3 {
  padding: 5px 10px !important;
  text-align: center;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  margin: auto;
  width: fit-content;
  transition: background-color 0.3s ease;
  animation: backgroundChange 1.5s infinite ease-in-out;
}

.button-link h3:hover {
  box-shadow: 0 0 0 5px purple;
  color: white;
  animation: none;
  width: fit-content;
}

@keyframes backgroundChange {
  0% {
    box-shadow: 0 0 0 0px #eaf0e1;
  }
  25% {
    box-shadow: 0 0 5px 5px #eaf0e1;
  }
  50% {
    box-shadow: 0 0 10px 5px #eaf0e1;
  }
  75% {
    box-shadow: 0 0 5px 5px #eaf0e1;
  }
  100% {
    box-shadow: 0 0 0 0px #eaf0e1;
  }
}

.button-link:hover {
  background-color: #06386d;
  box-shadow: 4px 4px 8px white;
}

.my-photo {
  display: flex;
  margin: auto;
}

.cover-container {
  display: flex;
  justify-content: center;
  margin: 3rem;
  font-size: 1.2rem;
  color: white;
}

.info-container {
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info-container a {
  padding: 3rem .70rem;
}

.references-list {
  display: flex;
  flex-direction: column;
  font-size: 2.5rem;
  align-items: center;
  margin: 2rem;
  text-shadow: 1px 1px 2px white, 0 0 1em blue, 0 0 0.2em blue;
  transition: transform 0.4s ease-in-out;
}

.references-list:hover {
  transform: scale(1.4);
}

.footer-container {
  text-align: center;
  color: red;
}
